
import Tabbar from 'vux/src/components/tabbar/tabbar.vue'
import TabbarItem from 'vux/src/components/tabbar/tabbar-item.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Icon from 'vux/src/components/icon/index.vue'
import Loading from 'vux/src/components/loading/index.vue'

import { mapState } from 'vuex'
export default {
components: {
Group,
XHeader,
Cell,
Tabbar,
TabbarItem,
Icon,
Loading
},
data () {
return {




msg: 'Hello World!',
state:'hello1',
}
},
mounted(){
this.state = window.location.hash.substr(2);
/* this.$http.get('https://www.vue-js.com/api/v1/topics').then(function(res){

})*/
},
computed:{
...mapState(['direction','headTit','tabberShow','ifShowbar']),
...mapState({
isLoading: state => state.isLoading
})
},
watch:{
'$route':function(){
this.state = window.location.hash.substr(2);
}
},
}



