export default {
    props:['type'],
  data(){
      return {
          show:true
      }
  },
  computed:{
    actionclass(){
        return {
          'alert_default':this.type=='default',
          'alert_success':this.type=='success',
          'alert_green':this.type=='green',
        }
    }
  }
}



